.login-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #1a237e 0%, #3f51b5 50%, #536dfe 100%);
  position: relative;
  overflow: hidden;
  transition: background 0.5s ease;

  // 다크 모드 배경
  &.dark-mode {
    background: linear-gradient(135deg, #0a0e27 0%, #1a1f3a 50%, #2a2f4a 100%);

    .login-card {
      background: #1e1e2e;
      border: 1px solid rgba(255, 255, 255, 0.1);
      box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5), 
                  0 8px 32px rgba(0, 0, 0, 0.3);

      .security-notice {
        background: linear-gradient(135deg, #2d2416, #3d3420);
        border: 1px solid rgba(255, 204, 2, 0.3);

        mat-icon {
          color: #ffb74d;
        }

        .notice-content {
          h4 {
            color: #ffb74d;
          }

          p {
            color: #e0e0e0;
          }
        }
      }

      .login-form {
        background: #1e1e2e;
        color: #e0e0e0;

        .form-field {
          .full-width {
            ::ng-deep .mat-mdc-text-field-wrapper {
              background-color: #2a2a3a !important;
            }

            ::ng-deep .mat-mdc-form-field-label {
              color: #b0b0c0 !important;
            }

            ::ng-deep .mat-mdc-form-field-input {
              color: #ffffff !important;
            }

            ::ng-deep .mdc-text-field--filled:not(.mdc-text-field--disabled) {
              background-color: #2a2a3a !important;
            }

            ::ng-deep .mat-mdc-form-field-focus-overlay {
              background-color: transparent !important;
            }

            // 비밀번호 표시 아이콘
            ::ng-deep .mat-mdc-icon-button {
              color: #b0b0c0 !important;
            }

            // 입력 필드 하단 라인
            ::ng-deep .mdc-line-ripple::after {
              border-bottom-color: #9fa8da !important;
            }

            // 에러 텍스트
            ::ng-deep .mat-mdc-form-field-error {
              color: #ff6b6b !important;
            }

            // 힌트 텍스트
            ::ng-deep .mat-mdc-form-field-hint {
              color: #b0b0c0 !important;
            }
          }
        }

        .error-message {
          background: rgba(255, 107, 107, 0.15) !important;
          color: #ff6b6b !important;
          border: 1px solid rgba(255, 107, 107, 0.3);

          mat-icon {
            color: #ff6b6b !important;
          }
        }
      }

      .links {
        background: #1e1e2e;

        .link-row {
          .link-item {
            color: #b0b0c0;

            a {
              color: #9fa8da !important;

              &:hover {
                color: #7986cb !important;
              }
            }

            mat-icon {
              color: #9fa8da !important;
            }
          }

          .separator {
            color: #505060;
          }
        }
      }

      .divider {
        background: #1e1e2e;

        .divider-line {
          background: #3a3a4a;
        }

        .divider-text {
          background: #1e1e2e;
          color: #808090;
        }
      }

      .register-section {
        background: #2a2a3a;
        border-top: 1px solid rgba(255, 255, 255, 0.1);

        p {
          color: #b0b0c0;
        }

        .register-link {
          color: #9fa8da !important;

          &:hover {
            color: #7986cb !important;
          }
        }
      }
    }
  }

  // 테마 토글 버튼
  .theme-toggle {
    position: absolute;
    top: 20px;
    right: 20px;
    z-index: 1000;
    background: rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(10px);
    color: white;
    width: 48px;
    height: 48px;
    border-radius: 50%;
    transition: all 0.3s ease;

    &:hover {
      background: rgba(255, 255, 255, 0.3);
      transform: scale(1.1) rotate(180deg);
    }

    mat-icon {
      font-size: 24px;
      width: 24px;
      height: 24px;
    }
  }

  .login-card {
    width: 100%;
    max-width: 460px;
    margin: 20px;
    background: var(--bg-primary);
    border-radius: 16px;
    box-shadow: 0 20px 60px rgba(0, 0, 0, 0.2), 
                0 8px 32px rgba(26, 35, 126, 0.4);
    overflow: hidden;
    z-index: 2;
    position: relative;
    border: 1px solid rgba(83, 109, 254, 0.2);

    .login-header.admin-header {
      background: linear-gradient(135deg, #1a237e, #3f51b5);
      color: white;
      padding: 24px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      position: relative;

      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: linear-gradient(45deg, transparent 30%, rgba(255, 255, 255, 0.1) 50%, transparent 70%);
        animation: shimmer 3s infinite;
      }

      .mode-indicator {
        display: flex;
        align-items: center;
        gap: 12px;
        z-index: 1;

        mat-icon {
          font-size: 28px;
          width: 28px;
          height: 28px;
          filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.3));
        }

        h2 {
          margin: 0;
          font-size: 20px;
          font-weight: 600;
          text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
        }
      }

      .mode-switcher {
        z-index: 1;

        .mode-switch-btn {
          color: white;
          border: 1px solid rgba(255, 255, 255, 0.4);
          border-radius: 8px;
          padding: 8px 12px;
          font-size: 12px;
          backdrop-filter: blur(10px);

          &:hover {
            background: rgba(255, 255, 255, 0.15);
          }
        }
      }
    }

    .security-notice {
      background: linear-gradient(135deg, #fff3e0, #ffe0b2);
      border: 1px solid #ffcc02;
      padding: 16px 24px;
      display: flex;
      align-items: flex-start;
      gap: 12px;

      mat-icon {
        color: #ff9800;
        font-size: 20px;
        width: 20px;
        height: 20px;
        margin-top: 2px;
      }

      .notice-content {
        flex: 1;

        h4 {
          margin: 0 0 4px 0;
          font-size: 14px;
          font-weight: 600;
          color: #e65100;
        }

        p {
          margin: 0;
          font-size: 12px;
          color: #bf360c;
          line-height: 1.4;
        }
      }
    }

    .login-form {
      padding: 32px 24px 24px;
      background: var(--bg-primary);
      color: var(--text-primary);

      .form-field {
        margin-bottom: 20px;

        .full-width {
          width: 100%;

          ::ng-deep {
            .mat-mdc-form-field-subscript-wrapper {
              margin-top: 4px;
            }

            .mat-mdc-text-field-wrapper {
              background-color: var(--bg-secondary);
              border-color: var(--primary-color);
            }

            .mat-mdc-form-field-label {
              color: var(--text-secondary);
            }

            .mat-mdc-form-field-input {
              color: var(--text-primary);
            }

            .mat-mdc-form-field-error {
              color: var(--error-color);
            }

            .mat-mdc-form-field-focus-overlay {
              background: rgba(63, 81, 181, 0.04);
            }
          }
        }
      }

      .error-message {
        display: flex;
        align-items: center;
        gap: 8px;
        background: #ffebee;
        color: #c62828;
        border: 1px solid #ef5350;
        padding: 12px 16px;
        border-radius: 8px;
        margin-bottom: 20px;
        font-size: 14px;

        mat-icon {
          font-size: 18px;
          width: 18px;
          height: 18px;
        }
      }

      .form-actions {
        margin-bottom: 24px;

        .login-btn {
          height: 48px;
          font-size: 16px;
          font-weight: 600;
          border-radius: 8px;
          background: linear-gradient(135deg, #1a237e, #3f51b5);
          color: white;
          display: flex;
          align-items: center;
          justify-content: center;
          gap: 8px;
          box-shadow: 0 4px 12px rgba(26, 35, 126, 0.4);

          &:hover {
            background: linear-gradient(135deg, #0d47a1, #1976d2);
            box-shadow: 0 6px 16px rgba(26, 35, 126, 0.5);
          }

          mat-spinner {
            ::ng-deep circle {
              stroke: white;
            }
          }
        }
      }

      .additional-links {
        text-align: center;

        .link-group {
          display: flex;
          align-items: center;
          justify-content: center;
          gap: 8px;

          .divider {
            color: var(--text-secondary);
            font-size: 12px;
          }

          button {
            font-size: 13px;
            min-width: auto;
            padding: 4px 8px;
            color: #3f51b5;
          }
        }
      }
    }

    .admin-info {
      padding: 20px 24px 24px;
      background: linear-gradient(135deg, #f3e5f5, #e8eaf6);
      border-top: 1px solid #b39ddb;

      .info-header {
        display: flex;
        align-items: center;
        gap: 8px;
        margin-bottom: 16px;
        color: #3f51b5;
        font-weight: 600;
        font-size: 14px;

        mat-icon {
          font-size: 18px;
          width: 18px;
          height: 18px;
        }
      }

      .info-content {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 12px;

        .info-item {
          display: flex;
          align-items: center;
          gap: 8px;
          padding: 10px;
          background: white;
          border-radius: 6px;
          border: 1px solid #c5cae9;
          font-size: 13px;
          color: var(--text-primary);
          box-shadow: 0 2px 4px rgba(63, 81, 181, 0.1);

          mat-icon {
            font-size: 16px;
            width: 16px;
            height: 16px;
            color: #3f51b5;
          }
        }
      }
    }
  }

  .background-decoration.admin-bg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;

    .decoration-shape {
      position: absolute;
      background: rgba(255, 255, 255, 0.08);

      &.shape-1 {
        width: 200px;
        height: 200px;
        top: -100px;
        right: -50px;
        transform: rotate(45deg);
        border-radius: 20px;
        animation: float 8s ease-in-out infinite;
      }

      &.shape-2 {
        width: 150px;
        height: 150px;
        bottom: -75px;
        left: -25px;
        border-radius: 50%;
        animation: float 10s ease-in-out infinite reverse;
      }

      &.shape-3 {
        width: 120px;
        height: 120px;
        top: 30%;
        left: -60px;
        transform: rotate(30deg);
        border-radius: 15px;
        animation: float 12s ease-in-out infinite;
      }
    }

    .admin-pattern {
      position: absolute;
      top: 50%;
      right: -200px;
      width: 400px;
      height: 400px;
      background: radial-gradient(
        circle at center,
        rgba(83, 109, 254, 0.1) 0%,
        rgba(83, 109, 254, 0.05) 40%,
        transparent 70%
      );
      border-radius: 50%;
      animation: pulse 6s ease-in-out infinite;
    }
  }
}

@keyframes float {
  0%, 100% {
    transform: translateY(0px) rotate(var(--start-rotation, 0deg));
  }
  50% {
    transform: translateY(-30px) rotate(var(--end-rotation, 10deg));
  }
}

@keyframes shimmer {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}

@keyframes pulse {
  0%, 100% {
    opacity: 0.6;
    transform: scale(1);
  }
  50% {
    opacity: 0.8;
    transform: scale(1.1);
  }
}

// 반응형 디자인
@media (max-width: 480px) {
  .login-container {
    padding: 20px;

    .login-card {
      margin: 0;
      border-radius: 12px;
      max-width: none;

      .login-header.admin-header {
        padding: 20px;

        .mode-indicator {
          h2 {
            font-size: 18px;
          }
        }
      }

      .security-notice {
        padding: 12px 20px;
      }

      .login-form {
        padding: 24px 20px 20px;

        .form-field {
          margin-bottom: 16px;
        }
      }

      .admin-info {
        padding: 16px 20px 20px;

        .info-content {
          grid-template-columns: 1fr;
          gap: 8px;
        }
      }
    }
  }
}